@font-face {
  font-family: Ideal Sans;
  src: url("/app/fonts/IdealSansSSm-Semibold.woff2") format("woff2");
  font-style: normal;
  font-weight: 700;
}
@font-face {
  font-family: Ideal Sans;
  src: url("/app/fonts/IdealSansSSm-Medium.woff2") format("woff2");
  font-style: normal;
  font-weight: 500;
}
@font-face {
  font-family: Ideal Sans;
  src: url("/app/fonts/IdealSansSSm-Book.woff2") format("woff2");
  font-style: normal;
  font-weight: 400;
}
@font-face {
  font-family: Quarto;
  src: url("/app/fonts/Quarto-Semibold.woff2") format("woff2");
}
:root {
  --font-body: "Ideal Sans", Arial, sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --dark-teal: rgb(49, 195, 189);
  --teal: rgb(101, 233, 228);
  --dark-orange: rgb(242, 177, 55);
  --orange: rgb(255, 200, 96);
  --neutral-700: rgb(26, 42, 51);
  --neutral-600: rgb(31, 54, 65);
  --neutral-300: rgb(168, 191, 201);
  --neutral-200: rgb(219, 232, 237);
  --drop-shadow: rgb(16, 33, 42);
  --drop-shadow-orange: #CC8B13;
  --drop-shadow-teal: #118C87;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
}

:root {
  font-family: var(--font-body), sans-serif;
  color: var(--white);
}

h1,
h2,
h3 {
  margin-top: 0;
  margin-bottom: 0;
}

.heading-L, .heading-M, .heading-S, .heading-XS {
  font-weight: 700;
  text-transform: uppercase;
}

.heading-L {
  font-size: 2.5rem;
  letter-spacing: 0.15625rem;
}

.heading-M {
  font-size: 1.5rem;
  letter-spacing: 0.09375rem;
}

.heading-S {
  font-size: 1.25rem;
  letter-spacing: 0.078125rem;
}

.heading-XS {
  font-size: 1rem;
  letter-spacing: 0.0625rem;
}

.heading-BODY {
  font-size: 0.875rem;
  letter-spacing: 0.05rem;
}

a,
a:visited,
a:active {
  text-decoration: none;
  color: var(--white);
}

.container {
  padding: 0 1.5rem;
  max-width: 28.75rem;
  margin: 0 auto;
  width: 100%;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
    transform: translate(0, 0);
  }
}

html {
  height: 100%;
}

body {
  height: 100%;
  background-color: var(--neutral-700);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.button {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: red;
  border-radius: 0.625rem;
  padding-top: 1.0625rem;
  padding-bottom: 1.5625rem;
}

.icon {
  background-color: red;
  /* defines the background color of the image */
  height: 2rem;
  width: 2rem;
}

.new-game-menu__icon--x,
.game-board__icon--x {
  mask: url(../assets/icon-x.svg) no-repeat center/contain;
  -webkit-mask: url(../assets/icon-x.svg) no-repeat center/contain;
}

.new-game-menu__icon--o,
.game-board__icon--o {
  mask: url(../assets/icon-o.svg) no-repeat center/contain;
  -webkit-mask: url(../assets/icon-o.svg) no-repeat center/contain;
}

.game-board__icon--restart {
  mask: url(../assets/icon-restart.svg) no-repeat center/contain;
  -webkit-mask: url(../assets/icon-restart.svg) no-repeat center/contain;
}

.game-board__winner-marker {
  mask: url(../assets/icon-o.svg) no-repeat center/contain;
  -webkit-mask: url(../assets/icon-o.svg) no-repeat center/contain;
}

.logo {
  width: 4.5rem;
  height: 2rem;
}

.game-board__nav {
  display: grid;
  grid-template: 1fr/1fr 1fr 1fr;
  align-items: center;
}
.game-board__turn-indicator {
  display: flex;
  justify-content: space-between;
  background-color: var(--neutral-600);
  box-shadow: inset 0 -4px 0 var(--drop-shadow);
  width: 140px;
  padding: 13px 30px 19px 30px;
  margin: auto;
}
.game-board__turn-indicator .icon {
  background-color: var(--neutral-300);
  height: 1.25rem;
  width: 1.25rem;
}
.game-board__restart-button {
  background-color: var(--neutral-300);
  padding: 1rem;
  width: min-content;
  margin-left: auto;
}
.game-board__restart-button .icon {
  background-color: var(--neutral-700);
  height: 1.25rem;
  width: 1.25rem;
}

.game-board__tile-grid {
  display: grid;
  grid-template: 1fr 1fr 1fr/1fr 1fr 1fr;
  column-gap: 20px;
  row-gap: 21px;
  margin: 1.1875rem 0;
}
.game-board__tile-grid .game-board__tile {
  height: 140px;
  width: 140px;
  background-color: var(--neutral-600);
  border-radius: 0.9375rem;
  box-shadow: inset 0 -8px 0 var(--drop-shadow);
}

.new-game-menu {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.new-game-menu__h1 {
  color: var(--neutral-300);
}
.new-game-menu__h2 {
  font-size: 0.875rem;
  text-transform: uppercase;
  font-weight: 400;
  color: var(--neutral-300);
}
.new-game-menu__marker-picker {
  display: flex;
  flex-direction: column;
  background-color: var(--neutral-600);
  padding: 1.5rem;
  padding-bottom: 1.875rem;
  border-radius: 0.9375rem;
  box-shadow: inset 0 -8px 0 var(--drop-shadow);
  width: 100%;
  margin: 2.5rem auto;
  color: var(--neutral-200);
  align-items: center;
}
.new-game-menu__marker-selection {
  display: flex;
  align-items: stretch;
  background-color: var(--neutral-700);
  border-radius: 0.625rem;
  padding: 0.5625rem 0.5rem;
  width: 100%;
  margin: 1.5rem auto 1.0625rem auto;
}
.new-game-menu__marker-selection .new-game-menu__button {
  width: 100%;
  padding: 0.6875rem;
}
.new-game-menu__marker-selection .new-game-menu__button:hover {
  cursor: pointer;
}
.new-game-menu__marker-selection .new-game-menu__button--dark {
  background-color: var(--neutral-700);
}
.new-game-menu__marker-selection .new-game-menu__button--dark .icon {
  background-color: var(--neutral-200);
}
.new-game-menu__marker-selection .new-game-menu__button--light {
  background-color: var(--neutral-200);
}
.new-game-menu__marker-selection .new-game-menu__button--light .icon {
  background-color: var(--neutral-700);
}
.new-game-menu__button-group {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.new-game-menu__button-group .new-game-menu__button {
  color: var(--neutral-700);
}
.new-game-menu__button-group .new-game-menu__button--orange {
  margin-bottom: 1.25rem;
  box-shadow: inset 0 -8px 0 var(--drop-shadow-orange);
  background-color: var(--dark-orange);
}
.new-game-menu__button-group .new-game-menu__button--teal {
  box-shadow: inset 0 -8px 0 var(--drop-shadow-teal);
  background-color: var(--dark-teal);
}

.hidden {
  display: none;
}